.auth__button {
  width: 358px;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  color: #000000;
  background-color: #FFFFFF;
  border: none;
  transition: opacity .3s linear;
  padding: 14px 0 14px 0;
  margin: 216px 0 0 0;
}

.auth__button:focus {
  border: none;
}

.auth__button:hover {
  opacity: .6;
}
